import { environment } from 'src/environments/environment';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { ApiEndpoints } from './../../constants/constants';
import { HttpService } from './../common/http.service';
import { Injectable } from '@angular/core';

@Injectable ({
  providedIn: 'root'
})
export class EmployeeService {

  constructor(private http: HttpService,
    private _function: FunctionService
) { }

grid(gridParams = { q: '', page: 0, limit: 10}) {
    const keys: any = Object.keys (gridParams);
    const value: any =  Object.values (gridParams);
      let query = '?';
      for (let i = 0; i < keys.length; i++) {
        if (value[i] !== '') {
          query += keys[i] + '=' + encodeURIComponent (value[i]) + '&';
        }
      }
   return this.http.get (ApiEndpoints.employeeGrid + query);
  }

  employeeXls() {
      return  environment.baseUrl + environment.apiVersion + ApiEndpoints.employeeXls;
  }

  employeeCsv() {
      return   environment.baseUrl+ environment.apiVersion + ApiEndpoints.employeeCsv;
  }
}
