import { toastMessages } from './../../core/constants/warning-messages';
import { ToastService } from 'src/app/core/helper/toast.helper.service';
import { SubSink } from 'subsink';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { ValidationruleService } from 'src/app/core/helper/validationrule.service';
import { EmployeeDetails } from 'src/app/core/interface/employe.interface';

@Component ({
  selector: 'app-employee-create',
  templateUrl: './employee-create.component.html',
  styleUrls: ['./employee-create.component.scss']
})
export class EmployeeCreateComponent implements OnInit, OnDestroy {
  public dropdownConfigKey = {
    displayKey: 'value',
    search: true,
    clearOnSelection: true,
    placeholder: 'select',
    height: '150px'
  };

  public employeeDetails : EmployeeDetails

  private subs = new SubSink ();

  public users = [
    {key: 1 , value: 'Rahul Vijayan' },
    {key: 1 , value: 'almas aliyar' }]

    public role = [
      'user','manager'
    ]

    public status = [
    'Active',
'InActive'
    ]

    public Designation = [
      'Sales Manager',
'Purchase Manager',
'Supervisor'
    ]

    public employeeDataForm!: FormGroup;

    public submitted = false;

    public id: string;

  constructor(
    private _router: Router,
    private _formBuilder: FormBuilder,
    private _rule: ValidationruleService,
    private _toast: ToastService,
    private activated: ActivatedRoute,) { }

ngOnInit(): void {
    this.setForm (true);
    this.getIdFromUrl ();
  }

  getIdFromUrl() {
    this.subs.add (
      this.activated.params.subscribe (res => {
          this.id = res['id'];
          if (this.id) {
            this.setForm (false);
          } else {
            this.setForm (true);
          }
      })
    );
  }

  get formControl() {
    return this.employeeDataForm.controls;
  }

  setForm(create: boolean) {
    if (create) {
      this.employeeDataForm = this._formBuilder.group ({
        firstName: ['', [this._rule.requiredValidation, this._rule.noWhitespaceValidator , this._rule.textBoxValidation ()]],
        lastName: ['', [this._rule.requiredValidation, this._rule.noWhitespaceValidator , this._rule.textBoxValidation ()]],
        email: ['', this._rule.emailValidation],
        contact: ['', [this._rule.requiredValidation]],
        reportingTo: ['', this._rule.requiredValidation],
        role: ['', this._rule.requiredValidation],
        hourChange: ['', this._rule.requiredValidation],
        overTimeCharge: ['', this._rule.requiredValidation],
        workingHours: ['', this._rule.requiredValidation],
        status: [''],
        designation: [''],
        address: ['', [this._rule.requiredValidation,  this._rule.textAreaValidation ()]]
      });
    } else {
      this.employeeDataForm = this._formBuilder.group ({
        firstName: ['', [this._rule.requiredValidation, this._rule.noWhitespaceValidator , this._rule.textBoxValidation ()]],
        lastName: ['', [this._rule.requiredValidation, this._rule.noWhitespaceValidator , this._rule.textBoxValidation ()]],
        email: ['', this._rule.emailValidation],
        contact: ['', [this._rule.requiredValidation]],
        reportingTo: ['', this._rule.requiredValidation],
        role: ['', this._rule.requiredValidation],
        hourChange: [, this._rule.requiredValidation],
        overTimeCharge: [0, this._rule.requiredValidation],
        workingHours: [0, this._rule.requiredValidation],
        status: [''],
        designation: [''],
        address: ['']
      });
    }
  }

  submit(){
    this.submitted = true;
    const formValue = this.employeeDataForm.value;
    if (this.employeeDataForm.invalid) {
      this._toast.error (toastMessages.validation);
      return;
    }
  this.create ();
  }

  create() {
    this._toast.success (toastMessages.created);
    this.view ();
  }

view(){
  this._router.navigate (['/employee-details']);
}

ngOnDestroy(): void {
  this.subs.unsubscribe ();
}
}
