import { CustomRenderComponent } from './../../core/render/custom-render/custom-render.component';
import { ApiStatus } from 'src/app/core/constants/constants';
import { EmployeeService } from './../../core/service/employee/employee.service';
import { SubSink } from 'subsink';
import { IdRenderComponent } from './../../core/render/id-render/id-render.component';
import { iconRender, idRender, Userid } from './../../core/interface/common.interface';
import { IconRenderComponent } from './../../core/render/icon-render/icon-render.component';
import { Router } from '@angular/router';
import { tableOption, tableConfig } from './../../core/constants/constants';
import { BadgeComponent } from './../../widget/badge/badge.component';
import { Component, OnInit, ViewChild, HostListener, OnDestroy } from '@angular/core';

@Component ({
  selector: 'app-employee-details-landing-page',
  templateUrl: './employee-details-landing-page.component.html',
  styleUrls: ['./employee-details-landing-page.component.scss']
})
export class EmployeeDetailsLandingPageComponent implements OnInit , OnDestroy {
  @ViewChild ('exportModal') exportModal: any;

  @ViewChild ('empModal') empModal: any;

  public tableOption = tableOption;

  public tableLimitOption = tableConfig.tableLimitOption;

  public tableLimit = tableConfig.tableLimit;

  public tableSearch = '';

  public currentPage = 1;

  public gridData:[];

  public totalCount = tableConfig.pager;

  public itemsPerPage = tableConfig.pager;

  public employeeDetail = {
    userId: '',
    employeeName: '',
    positionTitle: '',
    companyName: '',
    employeeType: '',
    deptName: '',
    hireDate: '',
    emailAddress: '',
    companyCode: '',
deptCode: '',
emailAddressOriginal: '',
employeeClass: '',
endDate: '',
firstName: '',
lastName: '',
managerUserId: '',
managerName: '',
oldEmployeeId: '',

  }

public tableSettings = {
    column: {
      UserId: {
        title: 'ID',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'custom',
        renderComponent: IdRenderComponent,
        valuePrepareFunction: (value :idRender, row) => {
          value = {
              id: row?.UserId,
              color: '#3298DB',
              event: 'view',
              row: row,
          };
          return value;
        }
      },
      FullName: {
        title: 'Employee Name',
        icon: '',
        type: 'text',
      },

      PositionTitle: {
        title: 'Position Title',
        icon: '',
        type: 'text',
      },

      CompanyName: {
        title: 'Company Name',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },

      EmployeeType: {
        title: 'Employee Type',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      DeptName: {
        title: 'Department Name',
        icon: '',
        type: 'text',
      },
      HireDate: {
        title: 'Hire Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      EmailAddress: {
        title: 'Email Address',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      CompanyCode: {
        title: 'Company Code',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
DeptCode: {
  title: 'Department Code',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
EmailAddress_Original: {
  title: 'Email Address Original',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
EmployeeClass: {
  title: 'Employee Class',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
EndDate: {
  title: 'End Date',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
FirstName: {
  title: 'First Name',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
LastName: {
  title: 'Last Name',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
ManagerUserId: {
  title: 'Manager User Id',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'custom',
  renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          if ( row?.ManagerUserId?.manager_id) {
            return row?.ManagerUserId?.manager_id;
        }
          return '-';
        }
},
ManagerUserName: {
  title: 'Manager Name',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'custom',
  renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          if ( row?.ManagerUserId?.manager_id) {
            return row?.ManagerUserId?.name;
        }
          return '-';
        }
},
OldEmployeeId: {
  title: 'Old Employee Id',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'text',
},
  }
  }

  private subs = new SubSink ();

  constructor( private _router: Router ,private _employeeService: EmployeeService) { }

ngOnInit(): void {
    this.gridData = [];
    this.employeeDetails ( );
  }

tablePageChange(offSet: number) {
    this.currentPage = (offSet);
    this.employeeDetails ( offSet , this.tableSearch, this.tableLimit);
  }

 employeeDetails( offSet = 1, search = this.tableSearch, limit = this.tableLimit) {
    this.currentPage = offSet;
    const gridParams = {
      q: search,
      page: offSet,
      limit: limit,
    };
    this.subs.add (
      this._employeeService.grid ( gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.gridData = res.employee?.result;
          this.totalCount =  res.employee?.total_count;
        }
      })
    );
  }

  search() {
    this.currentPage = 1;
    this.totalCount = this.tableLimit;
    this.employeeDetails ();
  }

openModal(){
    this.empModal.show ();
  }

  closeModal() {
    this.empModal.hide ();
  }

  tableEvent(event) {

if (event?.event === 'view' ) {
  this.employeeDetail = {
    userId: event?.row?.UserId,
    employeeName: event?.row?.FullName,
    positionTitle: event?.row?.PositionTitle,
    companyName: event?.row?.CompanyName,
    employeeType: event?.row?.EmployeeType,
    deptName: event?.row?.DeptName,
    hireDate: event?.row?.HireDate,
    emailAddress: event?.row?.EmailAddress,
    companyCode: event?.row?.EmailAddress,
deptCode: event?.row?.DeptCode,
emailAddressOriginal: event?.row?.EmailAddress_Original,
employeeClass: event?.row?.EmployeeClass,
endDate: event?.row?.EndDate,
firstName: event?.row?.FirstName,
lastName: event?.row?.LastName,
managerUserId: event?.row?.ManagerUserId,
managerName: event?.row?.ManagerUserName,
oldEmployeeId: event?.row?.OldEmployeeId,
  };
  this.openModal ();
}
  }

  create() {
    this._router.navigate (['/employee-details/create']);
  }

  openExportPopUp() {
    this.exportModal.show ();
  }

closeExportModal() {
    this.exportModal.hide ();
  }

  downloadCsv() {
    window.open (this._employeeService.employeeCsv (), '_self');
  }

downloadXls() {
    window.open (this._employeeService.employeeXls (), '_self');
  }

ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}
